import { Component, Input, OnInit } from '@angular/core';
import { map } from 'rxjs/operators';
import { Comment } from 'src/app/models/comment.model';
import { CommentService } from 'src/app/services/comment.service';

@Component({
  selector: 'app-comments-add',
  templateUrl: './comments-add.component.html',
  styleUrls: ['./comments-add.component.css']
})

export class CommentsAddComponent implements OnInit {
    @Input() articleKey?: string;
    @Input() key?: string;
    @Input() nickname?: string;

    comment: Comment = new Comment();
    submitted = false;

    constructor(public CommentService: CommentService) { }

    ngOnInit(): void
    {
        this.comment.updated_date = String(new Date());
    }

    ngOnChanges(): void
    {
        if(this.articleKey){
            this.comment.articleKEY = this.articleKey;
        }
        if(this.key)
        {
            this.comment.userKEY = this.key
        }
        if(this.nickname)
        {
            this.comment.userName = this.nickname
        }
    }

    saveComment(): void
    {
        this.comment.updated_date = String(new Date());

        this.CommentService.create(this.comment)
            .then( () => {
                console.warn('created successfully!');
            })
    }
}
