import { Component, OnInit } from '@angular/core';
import { map } from 'rxjs/operators';
import { Article } from 'src/app/models/article.model';
import { ArticleService } from 'src/app/services/article.service';

@Component({
    selector: 'app-articles-add',
    templateUrl: './articles-add.component.html',
    styleUrls: ['./articles-add.component.css']
})

export class ArticlesAddComponent implements OnInit {
    article: Article = new Article();
    submitted = false;

    constructor(private ArticleService: ArticleService) { }

    ngOnInit(): void 
    {
        this.article.title = 'Article Testing Demo';
        this.article.artisan = 'fengAdmin';
        this.article.ban = false;

        this.ArticleService.getAll()
            .snapshotChanges()
            .pipe(
                map(changes => 
                    changes.map(c => 
                        ({key: c.payload.key, ...c.payload.val() })
                    )
                )
            )
            .subscribe(data => {
                this.article.id = data.length + 1;
            });
        
    }

    saveArticle(): void
    {
        this.article.view = true;
        this.article.created_date = new Date();
        this.article.updated_date = new Date();
        this.ArticleService.create(this.article)
            .then( () => {
                console.warn(this.article);
                this.submitted = true;
            })
    }
    
    saveArticleRough(): void
    {
        this.article.view = false;
        this.article.created_date = new Date();
        this.article.updated_date = new Date();
        this.ArticleService.create(this.article)
            .then( () => {
                this.submitted = true;
            })
    }
}
