import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { ArticlesAddComponent } from './articles-add/articles-add.component';
import { ArticlesDetailsComponent } from './articles-details/articles-details.component';
import { ArticlesEditComponent } from './articles-edit/articles-edit.component';
import { ArticlesViewComponent } from './articles-view/articles-view.component';

const routes: Routes = [
  {
    path: '',
    component: ArticlesViewComponent
  },
  {
    path: 'add',
    component: ArticlesAddComponent
  },
  {
    path: ':id',
    component: ArticlesDetailsComponent
  },
  {
    path: 'edit/:key',
    component: ArticlesEditComponent
  }
];

@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule]
})
export class ComponentArticlesRoutingModule { }
