import { Component, Input, OnInit } from '@angular/core';
import { filter, map } from 'rxjs/operators';
import { Comment } from 'src/app/models/comment.model';
import { CommentService } from 'src/app/services/comment.service';

@Component({
    selector: 'app-comments-board',
    templateUrl: './comments-board.component.html',
    styleUrls: ['./comments-board.component.css']
})
export class CommentsBoardComponent implements OnInit {
    @Input() key?: string;
    commentData?: Comment[];

    allCount = 0;

    constructor(private CommentService: CommentService) { }

    ngOnInit(): void
    {
        this.retrieveComment();
        this.allCount = 0;
    }

    retrieveComment(): void
    {
        this.CommentService.getAll()
            .snapshotChanges()
            .pipe(
                map(changes => 
                    changes.map(c =>
                        ({ key: c.payload.key, ...c.payload.val() })
                    )
                ),
                // map(res =>
                //     res.filter(r =>
                //         r.key === this.key
                //     )
                // )
            ).subscribe(data => {
                // splice the Array
                this.commentData = data;
                // console.warn(data);
            });
    }

    // addCommnet() :void
    // {
    //     this.allCount = this.allCount + 1;
    //     // return this.allCount;
    // }
}
